input[type=text] {
  width: 100%;
  background-color: rgba(146, 146, 146, 0.527);
  border-radius: 1rem;
  border: none;
  height: 5rem;
  font-size: 2.5rem;
  font-weight: 400;
  color: rgb(236, 234, 234);
  direction: rtl;
  padding: 1rem;

  &:focus {
    outline: none;
  }
}
